// button
.com_btn {
  position: relative;
  padding: 0 30px;
  border-radius: 6px;
  min-width: 85px;
  @include inflex(all);
  letter-spacing: normal;
  color: #fff;
  gap: 8px;
  transition: 0.3s;
  &:visited,
  &:link,
  &:active,
  &:hover {
    color: #fff;
  }
  &_wrap {
    @include flex(x);
    gap: 10px;
    position: relative;
    &.between {
      @include flex(between);
    }
    &.flex {
      flex-wrap: wrap;
    }
    &.bottom {
      margin-top: 30px;
      @include media-min($m) {
        gap: 20px;
        .com_btn {
          min-width: 200px;
        }
      }
      @include media-max($l) {
        gap: 10px;
      }
      @include media-max($m) {
        .com_btn {
          flex: 1;
        }
      }
    }
    &.has_paging {
      margin-top: 70px;
      @include media-max($l) {
        margin-top: 20px;
      }
      @include media-max($m) {
        @include flex(column);
        gap: 10px;
        .com_paging {
          order: 2;
        }
      }
      .right {
        @include flex(y);
        gap: 10px;
        @include media-min($m) {
          position: absolute;
          right: 0;
          top: 0;
          .com_btn {
            min-width: 0;
          }
        }
        @include media-max($l) {
          top: 0;
        }
        @include media-max($m) {
          .com_btn {
            flex: 1;
          }
          order: 1;
        }
      }
    }
    &.right {
      justify-content: flex-end;
    }
    .com_paging {
      margin-top: 5px;
    }
  }
  &.full {
    width: 100%;
    flex: 1;
  }
  &.m_full {
    @include media-max($m) {
      width: 100%;
      flex: 1;
    }
  }
  &.l_full {
    @include media-max($l) {
      width: 100%;
      flex: 1;
    }
  }
  //&컬러 color
  &.point {
    background-image: linear-gradient(135deg, #0088ff, #7900c9);
    box-shadow: 5px 5px 15px 0px rgba(108, 52, 249, 0.3);
    color: #fff;
    &:hover {
      border-color: #5400c9;
      box-shadow: 0 0 15px 4px #6b4dff66;
    }
  }

  &.orange {
    background-color: $orange;
  }
  &.gray {
    background-color: $gray;
    color: #666;
    .dark & {
      background-color: #5b637a;
      color: #fff;
    }
  }
  &.dark_gray {
    background-color: #5b637a;
    color: #fff;
  }
  &.active {
    background-color: $gray;
    color: #fff;
  }
  &:disabled,
  &.disabled {
    background-color: #f1f0f5;
    color: #b1b3b7 !important;
  }
  //&유형 type
  &.line {
    background-color: transparent;
    border: 2px solid transparent;
    &.point {
      color: #fff;
      padding-inline: 0;
      position: relative;
      border-radius: 6px;
      background-image: linear-gradient($bg, $bg), linear-gradient(to right, #0088ff 0%, #7900c9 100%);
      background-origin: border-box;
      background-clip: content-box, border-box;
    }
    &.white {
      border-color: #fff;
      color: #fff;
      padding-inline: 0;
      transition: background-image 0.3s;
      &:hover {
        background-color: rgba(0, 0, 0, 0.1);
        box-shadow: 0 0 15px 4px #6b4dff66;
        position: relative;
        background-image: linear-gradient(to right, #0088ff 0%, #7900c9 100%);
        background-origin: border-box;
        background-clip: content-box, border-box;
      }
    }
    &.white_2 {
      border-color: rgba(#fff, 0.5);
      border-width: 1px;
      color: #fff;
      padding-inline: 0;
      transition: background-image 0.3s;
      &:hover {
        color: #fff;
        padding-inline: 0;
        background-origin: border-box;
        background-clip: content-box, border-box;
        box-shadow: 0 0 15px 4px #6b4dff66;
      }
    }
    &.dark {
      background-color: #fff;
      border: 1px solid #222;
      color: #222;
      .dark & {
        background-color: $bg-dark;
        color: #fff;
        border-color: #fff;
      }
    }
    &.gray {
      border-color: $c-border;
      background-color: transparent;
      color: #222;
      .dark & {
        color: #fff;
        border-color: $c-border-dark-light;
      }
    }
    &.xs {
      border-width: 1px;
    }
  }
  &.round {
    border-radius: 100%;
  }
  &.circle {
    border-radius: 100%;
    aspect-ratio: 1 / 1;
    padding: 0;
  }
  &.txt {
    padding: 0 !important;
    background: none;
    color: $point;
    min-width: auto;
    text-decoration: underline;
    text-underline-offset: 5px;
    &.gray {
      color: #919191;
    }
    &.black {
      color: $fc-default;
      .dark & {
        color: #fff;
      }
    }
  }
  //&사이즈 size
  &.l {
    height: 60px;
    font-weight: 700;
    font-size: 18px;
    min-width: 200px;
    @include media-max($m) {
      font-size: 16px;
      height: 50px;
    }
    &.oval {
      border-radius: 60px;
    }
  }
  &.m {
    height: 50px;
    font-weight: 600;
    //font-size: 12px; 14px 로 디자인 통일 11/11
    &.oval {
      border-radius: 50px;
    }
  }
  &.s {
    height: 36px;
    font-size: 14px;
    font-weight: 500;
    min-width: auto;
    &.oval {
      border-radius: 36px;
    }
  }
  &.xs {
    height: 30px;
    font-size: 12px;
    font-weight: 500;
    padding-inline: 13px;
    min-width: auto;
    &.oval {
      border-radius: 30px;
    }
  }
  &.xxs {
    min-width: auto;
    border-width: 1px;
    padding: 2px 5px;
    font-size: 12px;
  }
  &.full {
    width: 100%;
  }
  &.extend {
    flex: 1;
  }
  &.px_0 {
    padding-inline: 0;
  }
  //&아이콘
  .svg_icon {
    &.icon_search {
      width: 16px;
      min-width: 16px;
      height: 16px;
    }
  }
}

//아이콘버튼
.com_btn_edit {
  width: 26px;
  height: 26px;
  border-radius: 50%;
  background-color: #fff;
  @include flex(all);
}
.com_btn_mike {
  @include flex(all);
  width: 46px;
  height: 46px;
  border-radius: 50%;
  box-shadow: 0px 10px 10px 0px rgba(0, 0, 0, 0.16);
  background-color: $point;
}

/*!페이징 << < > >> */
.com_paging {
  margin-top: 70px;
  text-align: center;
  @include media-max($l) {
    margin-top: 50px;
  }
  @include media-max($m) {
    margin-top: 30px;
  }
  :is(.btn_first, .btn_prev, .btn_page, .btn_next, .btn_last) {
    min-width: 27px;
    height: 27px;
    text-align: center;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    vertical-align: middle;
    margin-left: 0;
    border-radius: 5px;
  }
  :is(.btn_first, .btn_prev, .btn_next, .btn_last) {
    border: 1px solid #d4d4d7;
    margin-inline: 5px;
    @include media-max($l) {
      margin-inline: 2px;
    }
    .dark & {
      border-color: #707070;
    }
    img {
      height: 16px;
    }
  }
  :is(.btn_first, .btn_prev) {
    transform: rotate(180deg);
  }
  .btn_prev {
    margin-right: 50px;
    @include media-max($l) {
      margin-right: 15px;
    }
  }
  .btn_next {
    margin-left: 50px;
    @include media-max($l) {
      margin-left: 15px;
    }
  }
  .btn_page {
    line-height: inherit;
    color: #707070;
    margin-inline: 10px;
    @include media-max($l) {
      margin-inline: 0px;
    }
    &.active {
      font-weight: bold;
      color: #222;
      border: 1px solid #666;
      .dark & {
        color: #fff;
        border-color: #fff;
      }
    }
    &.disabled {
      cursor: auto;
      opacity: 0.4;
    }
  }
}

//탭
.com_tab {
  margin-left: auto;
  display: flex;
  gap: 40px;
  justify-content: flex-end;
  @include media-max($l) {
    gap: 20px;
  }
  &__wrap {
    position: relative;
    @include media-min($l) {
      margin-right: 40px;
    }
  }
  .tab {
    position: relative;
    @include flex(all);
    justify-content: center;
    white-space: nowrap;
    color: $text-3;
    font-size: 20px;
    //padding-left: 20px;
    width: 120px;
    @include media-max($m) {
      width: 90px;
    }
    &::before {
      content: '';
      position: absolute;
      bottom: -10px;
      left: 0;
      transform: translateY(-50%);
      width: 0;
      background-image: linear-gradient(90deg, #95ff2b, #00c1ff);
      height: 2px;
      transition: 0.2s;
    }
    @include media-max($l) {
      font-size: 16px;
    }
    &.active {
      color: #fff;
      font-weight: 500;
      &::before {
        width: 100%;
      }
    }
  }
}
//서브링크
.com_sub_link {
  background-color: $point;
  .dark & {
    background-color: $inner-dark;
    border-bottom: 1px solid $c-border-dark-light;
  }
  &.center {
    .com_center_wrap {
      justify-content: center;
      @media screen and (max-width: 374.9px) {
        justify-content: flex-start;
      }
    }
  }
  .com_center_wrap {
    @include flex(s);
    padding: 30px 60px;
    gap: 40px;
    @include media-max($l) {
      gap: 15px;
      padding: 20px 0;
      overflow-x: auto;
      -ms-overflow-style: none;
      &::-webkit-scrollbar {
        display: none;
      }
    }
    .link {
      color: #206f70;
      font-weight: 700;
      font-size: 20px;
      @include media-max($l) {
        font-size: 16px;
        letter-spacing: -0.5px;
        white-space: nowrap;
      }
      .dark & {
        color: #fff;
      }
      &.active {
        color: #fff;
        .dark & {
          color: $point;
        }
      }
    }
  }
}
//서브링크 depth3
.com_sub_link2 {
  gap: 5px;
  margin-bottom: 20px;
  .com_btn {
    border-radius: 10px;
    font-size: 14px;
    font-weight: 600;
    padding-inline: 20px;
    min-width: auto;
    white-space: nowrap;
    height: 40px;
    &.active {
      background-color: $point;
      color: #fff;
      border-color: $point;
    }
    @include media-max($m) {
      padding-inline: 13px;
    }
  }
}

//Checkbox 커스텀
//&일반 checkbox , radio
//  <label class="com_chk"><input type="checkbox">윙</label> //마크업은 수정 불가
.com_chk {
  @include inflex(y);
  gap: 7px;
  cursor: pointer;
  line-height: initial;
  .dark & {
    color: #fff;
  }
  &__wrap {
    @include flex(y);
    gap: 10px 20px;
    @include flex-wrap;
    //&.col4 {
    //	@include list(5px, 4, 13px);
    //}
  }
  input[type='text'],
  input[type='number'] {
    width: 80px;
    text-align: center;
  }
  input[type='checkbox'],
  input[type='radio'] {
    display: inline-block;
    padding-right: 0;
    height: 24px;
    &::before {
      @include beforeafter;
      width: 24px;
      height: 24px;
      background: #fff;
      border: 2px solid #bdbfc2;
      border-radius: 2px;
      background-image: url('../../img/svg/icon_chk.svg');
      background-position: center;
      background-size: 12px auto;
      background-repeat: no-repeat;
      cursor: pointer;
      .dark & {
        background-color: transparent;
        border-color: #444a56;
        background-image: url('../../img/svg/icon_chk_dk.svg');
      }
    }
    &:checked::before {
      background-image: url('../../img/svg/icon_chk_on.svg');
      border-color: $point;
    }
    &:disabled {
      opacity: 0.4;
      border: none;
    }
  }
  &.circle {
    position: relative;
    margin: 0;
    input[type='checkbox'],
    input[type='radio'] {
      &::before {
        border-radius: 50%;
        background-image: none;
      }
      &:checked::before {
        background-image: url('../../img/svg/icon_radio_on.svg');
        background-size: 24px auto;
      }
    }
  }
}
.com_chk_btn {
  cursor: pointer;
  &__wrap {
    @include flex(y);
    gap: 5px;
    @include flex-wrap;
  }
  input {
    & + .btn {
      height: 40px;
      padding-inline: 15px;
      @include inflex(all);
      font-weight: 500;
      font-weight: 600;
      white-space: nowrap;
      border: 1px solid #222;
      border-radius: 6px;
      cursor: pointer;
      width: 100%;
      line-height: initial;
      @include media-max($l) {
        font-size: 14px;
      }
      .dark & {
        color: #fff;
        border-color: #fff;
      }
    }
  }
  input:checked {
    & + .btn {
      color: #fff;
      background-color: $point;
      border-color: $point;
    }
  }
  input:disabled {
    border: none;
    & + .btn {
      background-color: #e2e7ed !important;
      color: #b1b3b7 !important;
      border: none !important;
      cursor: default;
      .dark & {
        background-color: $gray-dark !important;
        color: #5b637a !important;
      }
    }
  }
  &.gray {
    input {
      & + .btn {
        background-color: $gray;
        color: #b1b3b7;
        border-color: $gray;
      }
      .dark &:not(:checked) + .btn {
        background-color: #5b637a;
        border-color: #5b637a;
        color: #fff !important;
      }
    }
    input:checked {
      & + .btn {
        color: #fff;
        background-color: $point;
        border-color: $point;
      }
    }
  }
  &.line {
    input {
      & + .btn {
        background-color: transparent;
        color: $point;
        border-color: $point;
        .dark & {
          border-color: $c-border-dark-light;
          color: #fff;
        }
      }
    }
    input:checked {
      & + .btn {
        color: #fff;
        background-color: $point;
        border-color: $point;
      }
    }
  }
  &.l {
    input + .btn {
      height: 50px;
      border-radius: 6px;
      font-weight: 400;
    }
  }
}
.com_board_sort {
  @include flex(s);
  //@include flex-wrap;
  gap: 10px;
  @include media-min($m) {
    @include flex-wrap;
  }
  @include media-max($l) {
    justify-content: center;
  }
  @include media-max($m) {
    justify-content: flex-start;
    gap: 5px;
    overflow-x: auto;
    width: 100%;
    white-space: nowrap;
    background-color: #fff;
    .dark & {
      background-color: $bg-dark;
    }
    &::-webkit-scrollbar {
      display: none;
    }
  }
  &__wrap {
    position: relative;
    width: 100%;
    @include media-max($m) {
      &:after {
        content: '';
        position: absolute;
        bottom: 0;
        right: 0;
        width: 50px;
        height: 40px;
        filter: blur(1px);
        background-image: linear-gradient(270deg, #fff 0, rgba(243, 245, 247, 0) 100%);
        .dark & {
          background-image: linear-gradient(270deg, $bg-dark 0, rgba(243, 245, 247, 0) 100%);
        }
      }
    }
  }
  &.type_date {
    .btn {
      min-width: auto;
      @include media-min($l) {
        padding-inline: 25px;
      }
    }
    @include media-max($l) {
      gap: 5px;
    }
    @include media-max($m) {
      @include list(5px, 5px, 5);
      .com_chk_btn {
        flex: 1;
        .btn {
          width: 100%;
        }
      }
    }
  }
  .com_chk_btn {
    .btn {
      font-size: 16px;
      padding-inline: 20px;
      border-radius: 10px;
      @include media-min($l) {
        min-width: 134px;
      }
    }
  }
}
.com_star {
  //별점 선택하기
  position: relative;
  direction: rtl;
  text-align: center;
  input[type='radio'] {
    display: none;
    + .icon_star {
      @include svg-icon-color('icon_star', 'BDBFC2');
    }
    &:checked {
      & ~ .icon_star {
        @include svg-icon-color('icon_star', 'FD4957');
      }
    }
    &:disabled {
      & + .icon_star {
        cursor: default;
        width: 18px;
        height: 18px;
        margin-right: 2px;
      }
    }
  }
  .icon_star {
    @include svg-icon-color('icon_star', 'BDBFC2');
    display: inline-block;
    cursor: pointer;
    margin-right: 4px;
    margin-left: 0 !important;
    &:first-of-type {
      margin-right: 0;
    }
    @include media-max($s) {
      margin-right: 0;
    }
  }
  //별점 보여주기
  &_display {
    width: 105px !important;
    min-width: 105px !important;
    @include media-max($l) {
      width: 95px !important;
      min-width: 95px !important;
    }
    &.svg_icon.icon_star,
    .svg_icon.icon_star {
      background-repeat: repeat-x !important;
      background-position: left !important;
      @include media-max($l) {
        height: 18px;
      }
    }
  }
}
.com_btn_like {
  input {
    display: none;
  }
  &:hover {
    cursor: pointer;
  }
  .svg_icon.icon_like {
    @include svg-icon-color('icon_like', 'fff');
    width: 20px !important;
    min-width: 20px !important;
    height: 16px !important;
    @include media-max($m) {
      width: 12px !important;
      min-width: 12px !important;
      height: 12px !important;
    }
  }
  &.type_circle {
    .button {
      background-color: rgba(0, 0, 0, 0.5);
      width: 27px;
      height: 27px;
      border-radius: 50%;
      @include inflex(all);
    }
    input:checked {
      & + .button {
        background-color: #fd4957;
      }
    }
  }
  &.type_icon {
    .svg_icon.icon_like {
      @include svg-icon-color('icon_like', '5B637A');
      .dark & {
        @include svg-icon-color('icon_like', 'fff');
      }
    }
    input:checked {
      & + .svg_icon.icon_like {
        @include svg-icon-color('icon_like_on', 'FD4957');
        .dark & {
          @include svg-icon-color('icon_like_on', 'FD4957');
        }
      }
    }
  }
}
.com_cart {
  input {
    display: none;
  }
  &:hover {
    cursor: pointer;
  }
  .svg_icon.icon_cart {
    @include svg-icon-color('icon_cart', 'fff');
    width: 14px !important;
    min-width: 14px !important;
    height: 14px !important;
  }
  &.type_circle {
    .button {
      background-color: rgba(0, 0, 0, 0.5);
      width: 46px;
      height: 46px;
      border-radius: 50%;
      @include inflex(all);
      @include media-max($l) {
        width: 40px;
        height: 40px;
      }
      @include media-max($m) {
        width: 34px;
        height: 34px;
      }
    }
    input:checked {
      & + .button {
        background-color: #fd4957;
      }
    }
  }
}
.com_btn_subscribe {
  &:hover {
    cursor: pointer;
  }
  .btn {
    height: 30px;
    padding-inline: 11px;
    @include flex(y);
    gap: 5px;
    border-radius: 30px;
    background-color: $point;
    color: #fff;
    font-weight: 600;
    .off {
      display: none;
    }
  }
  input {
    display: none;
    &:checked {
      & + .btn {
        .off {
          display: block;
        }
      }
    }
  }
}
.com_thumbup {
  @include flex(_);
  gap: 5px;
  line-height: initial;
  font-weight: 700;
  cursor: pointer;
  input {
    & + .svg_icon {
      @include svg-icon-color('icon_thumbup', '92949A');
    }
    &:checked {
      & + .svg_icon {
        @include svg-icon-color('icon_thumbup', '49BCBD');
      }
    }
  }
}

//info popup 버튼
.com_btn_info {
  @include flex(y);
  gap: 10px;
  justify-content: flex-end;
  margin-bottom: 10px;
  font-size: 16px;
  text-align: center;
  margin-left: auto;
  .dark & {
    color: #fff;
  }
}

//qr share 버튼

.com_btn_qr,
.com_btn_share {
  border-radius: 6px;
  border: 2px solid $point;
  @include flex(all);
  width: 54px;
  height: 54px;
  background-color: $bg-gray;
  background-repeat: no-repeat;
  background-position: center;
  .dark & {
    background-color: $bg-dark;
