//토글 스위치 버튼
.com_switch {
  &__wrap {
    position: relative;
    width: 40px;
  }
  width: 40px;
  height: 20px;
  display: inline-block;
  -webkit-appearance: none;
  background-color: #d9d9d9;
  border-radius: 100px;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  transition: all 0.3s ease;
  cursor: pointer;
  &:after {
    content: '';
    display: inline-block;
    width: 16px;
    height: 16px;
    background-color: #fff;
    border-radius: 50px;
    position: absolute;
    top: 50%;
    left: 2px;
    transform: translateY(-50%);
    transition: all 0.3s ease;
  }
  &:checked[type='checkbox'] {
    background-color: $point;
    transition: all 0.3s ease;
  }
  &:checked[type='checkbox']:after {
    left: 22px;
    transition: all 0.3s ease;
    background-color: #fff;
    border: none;
  }
}

.btn_scroll_top {
  position: fixed;
  right: 40px;
  bottom: 40px;
  z-index: 100;
  border-radius: 6px;
  width: 60px;
  height: 60px;
  @include inflex(all);
  border-radius: 50%;
  background-color: rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(#fff, 0.3);
  transition: 0.3s;
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5);
  @include media-max($m) {
    bottom: 20px;
    right: 20px;
    width: 40px;
    height: 40px;
    svg {
      width: 16px;
      height: auto;
    }
  }
  &:hover {
    color: #fff;
    padding-inline: 0;
    background-origin: border-box;
    background-clip: content-box, border-box;
    box-shadow: 0 0 15px 4px #6b4dff66;
    background-image: linear-gradient(#000, #000), linear-gradient(to right, #0088ff 0%, #7900c9 100%);
  }
}
