//section title
.sec_tit {
  @include flex(y); //가운데 정렬로 디자인 통일
  gap: 9px;
  font-size: 42px;
  font-weight: 700;
  margin-bottom: 50px;
  @include media-max($l) {
    font-size: 18px;
    margin-bottom: 20px;
  }
  .dark & {
    color: #fff;
  }
  .swiper_wrap + & {
    margin-top: 100px;
    @include media-max($l) {
      margin-top: 60px;
    }
  }
  &__wrap {
    @include flex(between);
    margin-bottom: 55px;
    @include media-max($l) {
      margin-bottom: 30px;
    }
    .swiper_wrap + & {
      margin-top: 100px;
      @include media-max($l) {
        margin-top: 60px;
      }
    }
    &.column {
      flex-direction: column;
    }
    .sec_tit {
      margin-bottom: 0;
      line-height: initial;
    }
    .sub {
      font-size: 20px;
      font-weight: 700;
      color: $point;
      align-self: flex-end;
      margin-left: 5px;
      margin-right: auto;
      padding-bottom: 3px;
      display: inline-block;
      @include media-max($l) {
        display: block;
        text-align: center;
        width: 100%;
        font-size: 14px;
      }
    }
    &.has_sort {
      @include media-max($l) {
        @include flex(column);
        @include flex(all);
        gap: 5px;
        .board_sort {
          margin-top: 20px;
          width: 100%;
          .select {
            width: 100% !important;
          }
        }
      }
      .select {
        width: 100px;
      }
    }
  }
}

//content title
.cont_tit {
  @include flex(y);
  gap: 10px;
  &__wrap {
    @include flex(between);
    gap: 10px;
  }
  font-size: 30px;
  font-weight: 700;
  margin-bottom: 20px;
  .dark & {
    color: #fff;
  }
  @include media-max($l) {
    font-size: 18px;
  }
}

.tag {
  font-weight: 500;
  color: #666;
  border-radius: 3px;
  border: 1px solid $c-border;
  padding: 5px;
  white-space: nowrap;
  .dark & {
    border-color: rgba(255, 255, 255, 0.2);
    color: #9da3b5;
  }
  &__wrap {
    @include flex(y);
    @include flex-wrap;
    gap: 5px;
  }
}

//!info icon
.info_icon {
  &__wrap {
    @include flex(column);
    gap: 10px;
    .info {
      margin-top: 0;
    }
  }
  @include flex(s);
  gap: 10px;
  .tit {
    color: #666;
    @include flex(y);
    gap: 2px;
    white-space: nowrap;
    .dark & {
      color: #c2c4c9;
    }
  }
  .desc {
    flex: 1;
    font-weight: 700;
    &.fw_normal {
      font-weight: 400;
    }
    .dark & {
      color: #fff;
    }
  }
}

//텍스트 사이즈
// xxl:24px, xl:20px, l:18px, m:16px, sm:14px xs:12px
.font_xl {
  font-size: 17px;
}
.font_xl_m {
  font-size: 17px;
  @include media-max($m) {
    font-size: 14px;
  }
}
.font_s {
  font-size: 12px;
}

