  letter-spacing: 0.5;
}

.typo_h_lg {
  @include fontSize(24, 28);
  line-height: 1.8;
  font-weight: 600;
  letter-spacing: 0.5;
}
.typo_h_md {
  @include fontSize(20, 26);
  line-height: 1.8;
  font-weight: 500;
  letter-spacing: 0.5;
}
.typo_xxl {
  @include fontSize(20, 24);
  line-height: 1.5;
  font-weight: 400;
  letter-spacing: 0.5;
}

.typo_xl {
  @include fontSize(18, 22);
  line-height: 1.5;
  font-weight: 400;
  letter-spacing: 0.5;
}

.typo_lg {
  @include fontSize(16, 20);
  line-height: 1.5;
  font-weight: 400;
  letter-spacing: 0.5;
}

.typo_md {
  @include fontSize(15, 16);
  line-height: 1.5;
  font-weight: 400;
  letter-spacing: 0.5;
}
.typo_sm {
  @include fontSize(13, 14);
  line-height: 1.5;
  font-weight: 400;
  letter-spacing: 0.5;
}

.typo_xs {
  @include fontSize(11, 12);
  line-height: 1.5;
  font-weight: 400;
  letter-spacing: 1;
}

//정보 카드
.info_card {
  background-color: $bg-gray;
  border-radius: 15px;
  padding: 20px 30px;
  @include flex(between);
  gap: 10px;
  align-items: center;
  flex: 1;
  align-self: stretch;
  @include media-max($l) {
    padding: 20px;
  }
  .dark & {
    background-color: $inner-dark;
  }
  &__wrap {
    @include flex(y);
    gap: 10px;
    @include media-max($l) {
      flex-direction: column;
    }
    &.column {
      @include flex(column);
      gap: 10px;
    }
  }
  .tit {
    font-weight: 700;
    @include media-min($m) {
      font-size: 17px;
    }
  }
  .desc {
    @include media-min($m) {
      font-size: 17px;
    }
  }
  .m_full {
    @include media-max($l) {
      width: 100%;
    }
  }
  .count {
    font-size: 30px;
    font-weight: 700;
    color: $point;
    .unit {
      font-size: 20px;
      font-weight: normal;
      color: #222;
    }
    @include media-max($l) {
      font-size: 16px;
      .unit {
        font-size: 14px;
      }
    }
  }
}
